button.user__option:hover div.user__option__dropdown {
  display: block;
}

button.user__option div.user__option__dropdown {
  right: 0;
  top: 24px;
  z-index: 100;
  background: #fff;
  padding: 0.9rem;
  padding-left: 0.5rem;
  text-align: left;
  border: 0.6px solid #ddd;
  z-index: 100;
  border-radius: 2px;
  width: 79px;
}

button.user__option > span.user__option__icon {
  display: inline-block;
  position: relative;
  top: 1px;
  right: 5px;
}

button.user__option div.user__option__dropdown::before {
  content: "";
  position: absolute;
  width: 11px;
  height: 11px;
  top: -19px;
  right: 9px;
  z-index: 10;
  border-bottom: 10px solid #fff;
  border-top: 10px solid transparent;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
}

button.user__option div.user__option__dropdown::after {
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  top: -12px;
  right: 7px;
  z-index: 1;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
  border-bottom: 13px solid #ddd;
  background: transparent;
}

div.user__option__dropdown > a,
div.user__option__dropdown > span {
  margin: 1rem 0;
  font-size: 0.8rem;
  font-weight: normal;
  letter-spacing: 0.9px;
  color: #254565;
}

div.user__option__dropdown span:hover,
div.user__option__dropdown a:hover {
